<template>
  <div>
    <div class="add">
      <router-link
        v-if="userInfo.project.nowCount < userInfo.project.maxCount"
        tag="a"
        :to="{ path: '/user/project-add', query: { status: 'add' }}"
      >
        <Icon type="md-add" style="margin-top:-2px;font-size:18px; margin-right:5px;" />项目发布
      </router-link>
      <router-link v-else tag="a" :to="{ path: '/vip'}">项目发布上限,了解会员权益发布更多项目</router-link>
    </div>
    <div class="bg">
      <div class="title">项目列表</div>
      <div style="border-bottom: 1px solid #eaeaea;padding-bottom:20px;">
        <Input
          v-model="seachConfig.name"
          placeholder="项目名称"
          style="width: 200px;margin-right: 10px;"
        />
        <i-Select
          v-model="seachConfig.type"
          style="width:130px;margin-right: 10px"
          placeholder="所有项目类型"
        >
          <i-Option value="-1">所有类型</i-Option>
          <i-Option value="1">个人项目</i-Option>
          <i-Option value="2">企业项目</i-Option>
          <i-Option value="3">政府项目</i-Option>
        </i-Select>
        <i-Select
          v-model="seachConfig.status"
          style="width:130px;margin-right: 10px"
          placeholder="所有项目状态"
        >
          <i-Option value="-1">所有项目状态</i-Option>
          <i-Option value="0">审核中</i-Option>
          <i-Option value="2">审核失败</i-Option>
          <i-Option value="1">融资中</i-Option>
          <i-Option value="3">系统冻结</i-Option>
          <i-Option value="4">终止融资</i-Option>
          <i-Option value="5">完成融资</i-Option>
        </i-Select>
        <Button icon="ios-search" type="primary" @click="getData">搜 索</Button>
        <br />
      </div>
      <div class="list">
        <br />
        <div v-if="data.length == 0" style="text-align: center;font-size:14px;color:#adadad;">暂时没有项目</div>
        <ul>
          <li v-for="(item, index) in data" :key="index">
            <div class="list_left">
              <div class="list_left_title">
                <span>
                  <img :src="item.logo == '' ? defaultLOGO : imgUrl+item.logo" alt />
                </span>
                <span class="list_left_name">{{item.name}}</span>
                <span
                  class="list_left_status"
                  :style="item.status == 0 ? 'color:#e29440'
                  :item.status == 1 ? 'color:#2868d1'
                  :item.status == 2 ? 'color:#f55231' 
                  :item.status == 3 ? 'color:#e29440' 
                  :item.status == 4 ? 'color:#666666' 
                  :item.status == 5 ? 'color:#2868d1' 
                  :''"
                >
                  <div
                    class="quan"
                    :style="item.status == 0 ? 'background:#e29440'
                    :item.status == 1 ? 'background:#2868d1'
                    :item.status == 2 ? 'background:#f55231' 
                    :item.status == 3 ? 'background:#e29440' 
                    :item.status == 4? 'background:#666666' 
                    :item.status == 5? 'background:#2868d1' 
                    :''"
                  ></div>
                  {{item.status == 0 ? '审核中'
                  :item.status == 1 ? '融资中'
                  :item.status == 2 ? '审核失败'
                  :item.status == 3 ? '系统冻结'
                  :item.status == 4 ? '终止融资'
                  :item.status == 5 ? '完成融资'
                  :'未知状态' }}
                </span>
              </div>
              <div class="cl"></div>
              <div class="list_left_content">
                <ul>
                  <li>
                    项目类型：
                    <Tag
                      color="primary"
                    >{{item.type == 1 ? '个 人' :item.type == 2 ? '企 业' :item.type == 3 ? '政 府' : '未知类型'}}</Tag>
                  </li>
                  <li>{{item.auditingName}}</li>
                  <li>联系人：{{item.contactName}}</li>
                  <li>当前轮次：{{item.nowRound}}</li>
                  <li>所属领域：{{item.territory}}</li>
                  <li>创建时间：{{item.createTime.split(' ')[0]}}</li>
                </ul>
                <div class="cl"></div>
                <div class="btns">
                  <Poptip
                    v-if="item.status== 1 || item.status == 2"
                    confirm
                    title="修改项目，项目将会重新审核请确认"
                    @on-ok="projectEdit(item.id)"
                  >
                    <Button style>修改项目</Button>
                  </Poptip>
                  <Button
                    v-if="item.status == 1"
                    :to="`/user/project-timeline-index/${item.code}`"
                  >项目进度</Button>

                  <Button
                    v-if="item.status != 0 "
                    :to="{path:`/user/project-treaty/${item.id}`}"
                  >投资协议</Button>

                  <Poptip
                    v-if="item.status == 1"
                    confirm
                    title="终止项目，终止后项目将完结"
                    @on-ok="clickStop(item.id)"
                  >
                    <Button>终止项目</Button>
                  </Poptip>
                  <Button :to="`/project/detail/${item.id}`">查看项目</Button>
                </div>
                <div class="cl"></div>
              </div>
            </div>
            <div class="list_right">
              <i-circle
                :size="150"
                :trail-width="4"
                :stroke-width="5"
                :percent="(item.actualMoney/item.nowMoney)*100 >= 100 ? 100 : (item.actualMoney/item.nowMoney)*100"
                stroke-linecap="square"
                :stroke-color="item.status == 0 ? '#e29440'
                :item.status == 1 ? '#2868d1'
                :item.status == 2 ? '#f55231' 
                :item.status == 3 ? '#e29440' 
                :item.status == 4? '#666666' 
                :item.status == 5? '#2868d1' 
                :''"
              >
                <div class="demo-Circle-custom">
                  <h1>{{item.nowMoney}}万</h1>
                  <p>总融资目标</p>
                  <span>
                    已融资
                    <i>{{item.actualMoney}}万</i>
                  </span>
                </div>
              </i-circle>
            </div>
            <div class="cl"></div>
            <div v-if="item.status == 2 || item.status == 3">
              <Alert
                :type="item.status== 2 ? 'error' : item.status== 3 ? 'warning' : ''"
                show-icon
              >{{item.status == 2 ? `审核失败：${item.fail} ( 修改项目后将重新进入审核 )` : item.status == 3 ? `系统冻结：${item.fail} ( 等待系统解冻或者联系管理员,如有疑问联系18966276820 )` :'错误状态'}}</Alert>
            </div>
          </li>
        </ul>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import {
  Icon,
  Button,
  Page,
  Poptip,
  Tag,
  Circle,
  Alert,
  Input,
  Select,
  Option
} from "iview";
import { projects, projectEnd } from "../../../api/project";
import { imgUrl } from "../../../utils";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Poptip.name]: Poptip,
    [Circle.name]: Circle,
    [Tag.name]: Tag,
    [Alert.name]: Alert,
    [Input.name]: Input,
    [Select.name]: Select,
    [Option.name]: Option,
    [Button.name]: Button
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      imgUrl: imgUrl,
      defaultLOGO: require("../../../assets/img/user.jpg"),
      //搜索配置
      seachConfig: {
        name: "",
        status: "-1",
        type: "-1",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      data: []
    };
  },
  methods: {
    //获取项目数据
    getData() {
      projects(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    },
    //项目终止
    clickStop(id) {
      projectEnd({ id }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.getData();
        }
      });
    },
    //跳转修改
    projectEdit(id) {
      this.$router.push({
        path: "/user/project-add",
        query: { status: "edit", id }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
