import Axios from 'axios'
import axiosConfig from './config'
if (Axios.defaults.baseURL == undefined) {
    axiosConfig()
}
/**
 * @function 添加项目
 */
export const project = (params) => {
    return Axios({
        method: 'post',
        url: `/project`,
        data: params
    })
}
/**
 * @function 修改项目
 */
export const projectEdit = (params) => {
    return Axios({
        method: 'put',
        url: `/project`,
        data: params
    })
}

/**
 * @function 根据ID获取项目
 * @param {id 项目ID}
 */
export const getProject = (params) => {
    return Axios({
        method: 'get',
        url: `/projectOne/${params.id}`,
    })
}
/**
 * @function 根据ID获取转换字段后的项目
 * @param {id 项目ID}
 */
export const getProjectUser = (params) => {
    return Axios({
        method: 'get',
        url: `/projectUser/${params.id}`,
    })
}
/**
 * @function 获取用户全部项目列表
 * @param {name 名字}
 * @param {type 类型}
 * @param {status 类型}
 * @param {pageNo 页}
 */
export const projects = (params) => {
    return Axios({
        method: 'get',
        url: `/projects?name=${params.name}&type=${params.type}&status=${params.status}&pageNo=${params.pageNo}`,
    })
}
/**
 * @function 终止项目
 * @param {id 项目id}
 */
export const projectEnd = (params) => {
    return Axios({
        method: 'post',
        url: `/projectEnd`,
        data: params
    })
}
/**
 * @function 获取项目进度
 * @param {code 项目code}
 * @param {pageNo 分页}
 * @param {type 类型}
 */
export const projectRate = (params) => {
    return Axios({
        method: 'get',
        url: `/project/rates?code=${params.code}&pageNo=${params.pageNo}&type=${params.type}`,
    })
}
/**
 * @function 添加项目进度
 */
export const projectRateAdd = (params) => {
    return Axios({
        method: 'post',
        url: `/project/rate`,
        data: params
    })
}
/**
 * @function 修改项目进度是否可见
 * @param {id 项目id}
 */
export const projectRateDelete = (params) => {
    return Axios({
        method: 'delete',
        url: `/project/rate`,
        data: params
    })
}

/**
 * @function 获取单个项目投资协议列表
 * @param {id 项目id}
 */
export const getRate = (params) => {
    return Axios({
        method: 'get',
        url: `/rate?code=${params.code}&pageNo=${params.pageNo}&name=${params.name}&status=${params.status}`,
    })
}

/**
 * @function 添加投资协议
 */
export const projectAddTermSheet = (params) => {
    return Axios({
        method: 'post',
        url: `/project/termSheet`,
        data: params
    })
}


